.footer {
  position: relative;
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: auto;
  background-color: #f6f3ff;
  padding: px-to-vw(100px, $desktop_md) 0 px-to-vw(90px, $desktop_md);

  // &::before
  //   content: ""
